@use 'sass:color';

$gap-width: 3rem;

.background {
  display: flex;
  justify-content: center;
  background: color.adjust(#242024, $lightness: -3.5%);
}

.container {
  // width: 100%;
  background: var(--bg-color);

  display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	align-content: flex-start;

  overflow: hidden;
}